  --divider-colour: rgba(255, 255, 255, 0.5);
  --font-colour: rgba(255, 255, 255, 0.85);
  --temp-font-colour: rgba(255, 255, 255, 1);
  --font-outline-colour: rgba(0, 0, 0, 0.4);
  /* --search-colour: rgba(255, 255, 255, 0.01); */
  --aa-muted-color-rgb: rgb(255, 255, 255);
  --panel-background: rgba(255, 255, 255, 0.2);
  --aa-background-color-alpha: 0.9;
  --aa-input-background-color-alpha: 0.3;
  --aa-text-color-rgb: 255 255 255;
  --aa-text-color-alpha: 0.9;
  --aa-overlay-color-rgb: 255 255 255;
  --aa-overlay-color-alpha: 1;
}

* {
  margin: 0;
  padding: 0;
  color: var(--font-colour);
  font-weight: 600;

  -webkit-text-stroke: 1px var(--font-outline-colour);
  /* border: 1px solid black; */
}
/* html {
  height: 100vh;
} */
h1 {
  font-size: 1.5rem;
}
h2 {
  font-size: 1.3rem;
  white-space: pre;
}

h3 {
  font-size: 1.05rem;
}

body {
  margin: auto;
  padding-top: 30px;
  height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100%;
  /* -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px); */
  /* position: relative; */
  /* z-index: 10; */

  font-family: 'Poppins', sans-serif;
}

main {
  width: 1450px;
  min-width: 1350px;
  max-width: 90vw;
  min-height: 850px;
  max-height: 850px;
  margin: auto;
  display: grid;
  gap: 2%;
  grid-template: minmax(auto, 100%) / 1fr 5fr;
  backdrop-filter: blur(1px);

  padding: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 25px;

  /* z-index: 0; */
}

#side-panel {
  grid-area: 1/1/2/2;
  min-width: 350px;
  max-height: 100%;
  padding: 1rem;
  display: grid;
  grid-template: auto auto / 1fr;

  background-color: var(--panel-background);
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  border-radius: 15px;
}

header {
  grid-area: 1/1/2/4;
  display: flex;
  justify-content: space-between;
  height: 50px;
}

header h1 {
  align-self: center;
}

/* TOP PANEL */
#top-panel {
  display: grid;
  grid-template: minmax(auto, 55px) auto / auto;
  border-bottom: 1.5px solid var(--divider-colour);
}

/* CURRENT-WEATHER */

#current-ctr {
  display: grid;
  grid-template: auto auto auto/ 1fr 1fr;
  align-content: start;
}

#location {
  white-space: normal;
  font-size: 1.2rem;
  overflow-wrap: break-word;
  border-right: 1.5px solid var(--divider-colour);
  /* padding-right: 1rem; */
}

#info {
  grid-area: 1/1/2/3;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#info :is(#location, .date-time-ctr) {
  padding: 1rem;
  flex-basis: 50%;
}

.date-time-ctr h2 {
  font-size: 1.1rem;
}

#current-ctr #icon-ctr {
  grid-area: 2/1/3/2;
  align-self: center;
  height: 7rem;
  width: 7rem;
}

#temp-output {
  font-size: 4rem;
  color: var(--temp-font-colour);
  align-self: center;
  grid-area: 2/2/3/3;
}

#feels-ctr {
  grid-area: 3/1/4/3;
  display: grid;
  grid-template: 1fr auto/ 1fr;
}
#feels-ctr .row {
  grid-area: 1/1/2/2;
  display: flex;
  align-items: center;
}

#feels-ctr h3 {
}
#feels-ctr img {
  height: 2.5rem;
  width: 2.5rem;
}
#feels-like {
  grid-area: 2/1/3/2;
  font-size: 2rem;
  padding-left: 0.7rem;
}
/*DAILY FORECAST*/

#daily-forecast {
  display: flex;
  flex-direction: column;
  gap: 10px;
  grid-area: 2/1/3/2;
  margin: 10px;
}

.daily-list {
  /* height: 100%; */
  display: grid;
  grid-auto-flow: row;
  gap: 3%;
}

.card {
  display: grid;
  grid-template-columns: 0.8fr 2fr 1fr;
  gap: 2.5%;
}

.daily-list .col {
  display: flex;
  flex-direction: column;
}

output.date {
  font-size: 0.8rem;
}

.icon {
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: center;

  border-radius: 4px;
}
.icon img {
  height: 3rem;
  width: 3rem;
}

.temp {
  border-left: 1.5px solid var(--divider-colour);
  justify-content: center;
}
output:is(.high, .low) {
  text-align: right;
}

/* SEARCH FORM  */
#search-form {
  grid-area: 1/1/2/2;
  margin-bottom: 1%;
  color: var(--search-colour);
}
.ais-search-box {
  max-width: none;
  /* color: var(--search-colour); */
}
.aa-Item {
  color: black;
}
/* UNIT TOGGLE*/
#toggle-ctr {
  display: flex;
}

#toggle-ctr img {
  height: 3rem;
  width: 3rem;
}
.switch {
  position: relative;
  display: inline-flex;
  align-self: center;
  align-items: center;
  width: 60px;
  height: 34px;
}

#unit-toggle {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  align-self: center;
  vertical-align: middle;
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: whitesmoke;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

#unit-toggle:checked + .slider {
  background-color: #2196f3;
}

#unit-toggle:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

#unit-toggle:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* input {
  font-size: 1.5rem;
  width: 80%;
  height: 100%;
}

input::placeholder {
  opacity: 0.5;
} */
/* button {
  width: 3rem;
  height: 100%;
  cursor: pointer;
} */

#main-panel {
  grid-area: 1/2/2/3;

  height: 100%;
  width: 100%;
  display: grid;
  grid-template: auto auto auto/ 1fr 3fr;

  gap: 15px;
}

#current-conditions h2 {
  font-size: 2.5rem;
}

/* WEATHER HIGHLIGHTS  */
#highlights {
  grid-area: 2/2/3/3;
  display: flex;
  justify-content: flex-end;
}
#highlights ul {
  display: grid;
  grid-template: 1fr 1fr / repeat(4, 1fr);

  align-self: center;
}

#highlights li {
  display: grid;
  grid-template: 1fr 1fr/1fr 3fr;
}
#highlights label {
  font-size: 0.8rem;
}
#highlights img {
  height: 3rem;
  width: 3rem;
  grid-area: 1/1/3/2;
}

#description-output {
  font-size: 2rem;
  margin-left: 2rem;
}

#charts-ctr {
  grid-area: 3/1/4/3;
  justify-self: center;
  height: 96%;

  width: 100%;
  display: flex;
  justify-content: center;

  padding-top: 15px;
  background-color: var(--panel-background);
  border-radius: 10px;
  /* border: 1px solid rgba(255, 255, 255, 0.3); */
}

.chart-ctr {
  border: none;
  width: 90%;
  display: flex;
  height: 100%;
}

.colSmall {
  border: none;

  width: 35px;
}

.colLarge {
  border: none;
  max-width: 100%;
  overflow-x: scroll;
}
.box {
  border: none;
  width: calc(2000px - 28px);
  height: 100%;
}
canvas {
  border: none;
}
